.films-vertical-list {
    margin-left: 30px;
    width: 1050px;

    > h2 {
        font-size: 25px;
        font-weight: 600;
    }

    > section {
        display: flex;
        flex-wrap: wrap;

        > div {
            width: 180px;
            border-radius: 8px;
            overflow: hidden;
            border: 1px solid rgb(227, 227, 227);
            box-shadow: 0 2px 8px rgb(0 0 0 / 10%);
            flex-shrink: 0;
            margin-top: 30px;
            margin-right: 30px;

            > a {
                width: 100%;

                > img {
                    width: 100%;
                    height: 273px;
                    object-fit: cover;
                    cursor: pointer;
                }
    
                > .no-image {
                    width: 100%;
                    height: 273px;
                    background-color: #DBDBDB;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
    
                    > img {
                        width: 96px;
                        height: 96px;
                        object-fit: cover;
                    }
                }
            }

            > div:last-child {
                position: relative;
                padding: 25px 10px 10px 10px;

                > .movie-percentage {
                    top: -25px;
                    position: absolute;
                }

                > h4 {
                    font-weight: 700;
                    cursor: pointer;
                    display: inline;

                    &:hover {
                        color: rgb(1, 180, 228);
                        transition-timing-function: ease-out;
                    }
                }

                > p {
                    color: rgba(0, 0, 0, 0.6);
                }
            }
        }
    }

    > span {
        width: 1020px;
    }

    > button {
        background-color: rgb(1, 180, 228);
        color: white;
        font-weight: 700;
        font-size: 24px;
        padding: 10px 0;
        margin-top: 30px;
        border-radius: 8px;
        cursor: pointer;
        border: none;
        outline: none;
        width: 1020px;

        &:hover {
            color: #0a1526;
            transition: color 0.05s ease-out;
        }
    }
}

@media only screen and (max-width: 1090px) {
    .films-vertical-list {
        width: 840px;

        > span {
            width: 810px;
        }

        > button {
            width: 810px;
        }
    }
}

@media only screen and (max-width: 880px) {
    .films-vertical-list {
        width: 630px;

        > span {
            width: 600px;
        }

        > button {
            width: 600px;
        }
    }
}

@media only screen and (max-width: 670px) {
    .films-vertical-list {
        width: 420px;

        > span {
            width: 390px;
        }

        > button {
            width: 390px;
        }
    }
}

@media only screen and (max-width: 460px) {
    .films-vertical-list {
        width: 180px;
        margin-left: 0;

        > section {
            > div {
                margin-right: 0;
            }
        }

        > span {
            width: 180px;
        }

        > button {
            width: 180px;
        }
    }
}