.modal {
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-modal {
    display: flex;
    width: 856px;
    height: 743px;

    > img {
        width: 60%;
        height: 100%;
        object-fit: cover;
    }

    > div {
        width: 40%;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;

        > img {
            width: 33px;
            height: 33px;
            object-fit: contain;
            cursor: pointer;
            padding: 7px;
            margin: 10px 10px 0 0;
        }

        > div {
            width: 100%;
            padding-bottom: 300px;

            > h4 {
                font-weight: 400;

                &:nth-of-type(1) {
                    padding-bottom: 14px;
                    border-bottom: 1px solid #e3e3e3;
                    padding-left: 20px;
                }

                &:nth-of-type(2) {
                    cursor: pointer;
                    margin-left: 20px;
                    display: inline-block;
                    
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            > p {
                font-size: 13px;
                margin-left: 20px;
                margin-top: 20px;
                cursor: default;
            }

            > span {
                margin-left: 20px;
                cursor: pointer;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

@media only screen and (max-width: 930px) {
    .image-modal {
        width: 656px;
        height: 543px;

        > div {
            > div {
                padding-bottom: 200px;
            }
        }
    }
}

@media only screen and (max-width: 730px) {
    .image-modal {
        width: 456px;
        height: 343px;

        > div {
            > div {
                padding-bottom: 80px;
            }
        }
    }
}

@media only screen and (max-width: 500px) {
    .image-modal {
        > div {
            > div {
                > h4 {
                    &:nth-of-type(1) {
                        padding-left: 10px;
                    }

                    &:nth-of-type(2) {
                        margin-left: 10px;
                    }
                }

                > p, span {
                    margin-left: 10px;
                }
            }
        }
    }
}