.recommendations {
    padding: 30px 0;
    border-bottom: 1px solid #d7d7d7;

    > h2 {
        font-size: 22px;
        font-weight: 600;
    }

    > section {
        margin-top: 20px;
        overflow-x: auto;
        padding-bottom: 10px;

        &::-webkit-scrollbar {
            height: 8px;
        }
    
        &::-webkit-scrollbar-thumb {
            background-color: lightgray;
            border-radius: 8px;
        }

        > div {
            width: 250px;
            flex-shrink: 0;
            margin: 0 8px;

            &:first-child {
                margin-left: 0;
            }
            
            > a {
                color: black;

                > div {
                    height: 141px;
                    cursor: pointer;
                    border-radius: 8px;
                    overflow: hidden;
                    position: relative;
    
                    > img {
                        width: 100%;
                        height: 100%;
                        object-fit: contain;
                    }
    
                    > .no-image {
                        background-color: #DBDBDB;
                        width: 100%;
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
    
                        > img {
                            width: 80px;
                            height: 80px;
                            object-fit: cover;
                        }
                    }
    
                    &:hover {
                        > .date {
                            opacity: 1;
                        }
                    }
    
                    > .date {
                        opacity: 0;
                        display: flex;
                        align-items: center;
                        background-color: rgba(255,255,255,0.9);
                        width: 100%;
                        height: 40px;
                        position: absolute;
                        bottom: 0;
                        padding-left: 10px;
                        transition: opacity 50ms ease-in-out;
    
                        > img {
                            width: 16px;
                            height: 16px;
                            object-fit: cover;
                            margin-right: 4px;
                        }
                    }
                }
            }

            > div {
                display: flex;
                justify-content: space-between;
                margin-top: 4px;

                > a {
                    color: black;
                    text-decoration: none;

                    > h4 {
                        cursor: pointer;
                        font-weight: 400;
                        padding-right: 20px;
                    }
                }
            }
        }

        > p {
            display: flex;
            align-items: center;
        }
    }
}